.bg-R{
    background-image: url(../../images/Artwork/R.png);
    background-repeat: no-repeat;
    background-size: contain;
    min-height: 100vh;
}
.card-backage{
    background-color: white;
    color: #0A3442;
    transition: 0.7s;
}
.btn-only-on-hover{
    display: none;
    transition: 0.7s;
}
.card-backage:hover{
    background-color: #0A3442;
    color: white;
}
.card-backage:hover > .btn-only-on-hover{
    display: block !important;

}
.form-long-term label + .MuiInput-formControl {
    margin-top: 38px !important;
}
.form-long-term .MuiInput-formControl {
    margin-top: 20px !important;
}
.custom-radio {
    border : 1px #DAECF4 solid;
    border-radius: 20px;
}
.custom-radio:hover , .custom-radio:active , .custom-radio:focus{
    border : 1px #DAECF4 solid;
    background-color: #DAECF4;
    border-radius: 20px;
}
