.bg-header{
    background-image: url(../../images/Artwork/limousine-banner.jpg);
    min-height: 130vh;
    background-size: cover;
    background-repeat: no-repeat;
}
.step-box{
    height: 65px;
    width: 65px;
    border-radius: 20px !important;
    background-color: transparent;
    transition: 0.5s;
}
.step-box:hover{
    height: 75px;
    width: 75px;
    background-color: #FBB040;
}
.bg-corporate-rental{
    background: url(../../images//Artwork/corporate-car.png) no-repeat;
    background-size: cover;
    min-height: 30rem;
}
.h-corporate-listitem{
    max-width: 18rem;
    min-height: 4rem;
}

.feedBack-list:nth-child(odd){
    background-color: #E3E5E9;
    color : #0A3442; 
    margin-left: 5rem;
}
.feedBack-list:nth-child(even){
    background-color: #0A3442;
    color : white;
}

.custom-checkbox span{
    font-size: 14px;
    color : #6C929F !important;
}
@media only screen and (max-width: 1024px) {
    .bg-header{
        min-height: 770px;
         background-size: contain;
         background-position: left;
     }
}
@media only screen and (max-width: 600px) {
    .feedBack-list:nth-child(odd) ,.feedBack-list:nth-child(even) {
        margin-left: 0;
        text-align: center;
    }
    .bg-header{
       min-height: 100vh;
        background-size: auto;
        background-position: left;
        background-image: url(../../images/Artwork/limousine-banner-mob.png);
    }

  }