/* general styles */
button , button:hover , input , input:focus{
    outline: none !important;
    box-shadow: none !important;
}
a,a:hover{
    text-decoration: unset !important;
}
.bg-darkblue{
    background-color: #0A3442 !important;
}
.text-darkblue{
    color: #185B71 !important;
}
.text-deepblue{
    color: #0A3442 !important;
}

.text-lightblue{
    color: #6C929F !important;
}
.text-lightorange{
    color: #FBB040 !important;
}
.bg-lightorange{
    background-color: #FBB040 !important;
}
.bg-lightblue{
    background-color: #F8FDFF;
}
.bg-dustblue{
    background-color: #6C929F !important;
}
.bg-orange{
    background-color: #FBB040 !important;
}
.bg-deepblue{
    background-color: #0A3442 !important;
}
.btn-orange{
    background-color: #FBB040 !important;
    color: white !important;
    border-radius: 30px;
    border : 1px solid #FBB040;
    font-size: 16px;
    padding: .25rem 1.75rem !important;
}
.btn-transparent{
    background-color: transparent;
    color: #0B3543 !important;
    border-radius: 30px;
    border : 1px solid #707070;
    font-size: 16px;
    padding: .25rem 1.75rem !important;
}
.btn-darkblue{
    background-color: #0B3543;
    color: white !important;
    border-radius: 30px;
    border : 1px solid #0B3543;
    font-size: 16px;
    padding: .25rem 1.75rem !important;

}
.p-relative{
    position: relative!important;
}
.p-absolute{
    position: absolute!important;
}

.custom-shadow{
    box-shadow: 0px 0px 10px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 8px rgba(255, 255, 255, 0.19) !important;
    text-align: center;
}
.btn-primary:not(:disabled):not(.disabled).active:focus,
 .btn-primary:not(:disabled):not(.disabled):active:focus, 
 .show>.btn-primary.dropdown-toggle:focus , .btn-primary.focus, .btn-primary:focus {
    box-shadow: 0 0 0 0.2rem rgb(251 176 64 / 47%) !important;
    border-color: rgb(251 176 64 / 47%) !important;
    outline: none !important;
}
/* home */
/* car offers section */
.overlay{
    background-color: #1659705e;
    height: 100%;
    width : 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    transition: 0.5s !important;
}
.overlay:hover{
    background-color: #16597018;
}
.d-web{
    display: block !important;
  }
  .d-mob{
    display: none !important;
  }
@media (min-width: 1200px){
    .container, .container-lg, .container-md, .container-sm, .container-xl {
        max-width: 1250px !important;
    }
}
@media (max-width: 540px){
    .d-web{
      display: none !important;
    }
    .d-mob{
      display: block !important;
    }
}